html {
  scroll-behavior: smooth;
}

body {
  -ms-overflow-style: none;
  scrollbar-width: none;
  cursor: url("cursor.5cf845f2.svg"), auto;
  background-color: #1d1d1d;
  margin: 0;
  font-family: Poppins, sans-serif;
  overflow-x: hidden;
}

html::-webkit-scrollbar {
  display: none;
}

body::-webkit-scrollbar {
  display: none;
}

* {
  box-sizing: border-box;
  margin: 0;
}

a, a:hover, a:focus, a:active {
  color: inherit;
  text-decoration: none;
}

.defaultButton {
  cursor: pointer;
  color: #1d1d1d;
  border: 0;
  border-radius: 15vw;
  width: 100%;
  height: 15vw;
  font-family: Poppins, sans-serif;
  font-size: 5vw;
  font-weight: 800;
  transition: box-shadow .3s linear;
}

.defaultButton:hover {
  box-shadow: 1px 4px 16px #e8e8f066;
}

.defaultButton:active {
  box-shadow: 1px 4px 16px #e8e8f0a3;
}

@media (width >= 744px) {
  .defaultButton {
    height: 8vw;
    font-size: 2.5vw;
  }
}

@media (width >= 1260px) {
  .defaultButton {
    height: 4vw;
    font-size: 2.5vw;
  }
}

.defaultInput {
  box-sizing: border-box;
  cursor: pointer;
  color: #1d1d1d;
  background-color: #f8f8fa;
  border: 0;
  border-radius: 6vw;
  outline: none;
  width: 100%;
  height: 12vw;
  padding-left: 14px;
  font-family: Poppins, sans-serif;
  font-size: 14px;
  font-weight: 400;
  transition: box-shadow .3s linear;
}

.defaultInput::-moz-placeholder {
  color: #7e7e83;
  opacity: 1;
}

.defaultInput::placeholder {
  color: #7e7e83;
  opacity: 1;
}

.defaultInput::-moz-placeholder {
  color: #7e7e83;
}

.defaultInput:hover {
  border: 1px solid #d5d7de;
}

.defaultInput:focus, .defaultInput:active {
  border: 1px solid #292929;
}

@media (width >= 744px) {
  .defaultInput {
    height: 7vw;
  }
}

@media (width >= 1260px) {
  .defaultInput {
    height: 5vw;
    padding-left: 1.5vw;
    font-size: 1.5vw;
  }
}

.defaultArea {
  cursor: pointer;
  color: #1d1d1d;
  resize: none;
  background-color: #f8f8fa;
  border: 1px solid #0000;
  border-radius: 6vw;
  outline: none;
  width: 100%;
  padding: 14px;
  font-family: Poppins, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.defaultArea::-moz-placeholder {
  color: #7e7e83;
  opacity: 1;
}

.defaultArea::placeholder {
  color: #7e7e83;
  opacity: 1;
}

.defaultArea::-moz-placeholder {
  color: #7e7e83;
}

.defaultArea:hover {
  border: 1px solid #d5d7de;
}

