.navigation {
  color: #1d1d1d;
  pointer-events: none;
  opacity: 0;
  z-index: -10;
  background-color: #c2c2c2;
  grid-auto-rows: min-content;
  gap: 10vw;
  width: 100vw;
  height: 100vh;
  padding: 5vw 20px 20px;
  transition: opacity .5s linear;
  display: grid;
  position: absolute;
  top: 0;
  left: 0;
}

@media (width >= 744px) {
  .navigation {
    gap: 7vw;
    padding: 5vw 10vw;
  }
}

@media (width >= 1260px) {
  .navigation {
    gap: 2vw;
    min-height: 100vh;
    padding-bottom: 15vw;
  }
}

.navigation__logo {
  grid-area: 1 / 1 / 2 / 2;
  justify-self: start;
}

@media (width >= 744px) {
  .navigation__logo {
    transform-origin: 0 0;
    scale: .8;
  }
}

.navigation__logo > path {
  transition: fill 3s linear;
}

.navigation__logo > path:hover {
  fill: #fff;
  transition: fill .3s ease-out;
}

.navigation__cross {
  cursor: pointer;
  border-radius: 2vw;
  grid-area: 1 / 1 / 2 / 2;
  justify-self: end;
  transition: background-color 1s linear, rotate .5s linear;
}

.navigation__cross:hover {
  background-color: #fff;
  border-radius: 50%;
  rotate: 360deg;
}

@media (width >= 744px) {
  .navigation__cross {
    border-radius: 100px;
    width: 40px;
    height: 40px;
  }

  .navigation__cross:hover {
    border-radius: 100px;
  }
}

.navigation__links {
  grid-area: 2 / 1 / 3 / 2;
}

.navigation__link {
  text-transform: uppercase;
  letter-spacing: 2px;
  border-radius: 10px;
  width: fit-content;
  margin-bottom: 5vw;
  padding-right: 10px;
  font-size: 7vw;
  font-weight: 600;
  transition: transform .2s ease-in-out, text-shadow .3s linear;
  display: block;
  transform: translateY(0);
}

.navigation__link:hover {
  text-shadow: 0 0 20px #fff, 0 0 15px #fff, 0 0 10px #fff, 0 0 5px #fff;
  transform: translateY(-1vw);
}

.navigation__link:last-child {
